@import '~node_modules/modern-normalize/modern-normalize.css';
:root {
  --vertical-padding: padding: 0 32.5px;
  --font: "DM Sans", sans-serif;
  --text-color-white: #ffffff;
  --text-color-black: #000;
}

:root {
  /* colors */
  --color-black: #000000;
  --color-bg-red: #ae1332;
  --color-green: #a4cb66;
  --color-yellow: #f5ce13;
  --color-white: #ffffff;
  --color-blue: #4d5ae5;
  --color-red: #ff0000;
  /* shadows */
  --var-1-shadow: 0px 4px 70px rgba(0, 0, 0, 0.1);
  /* animation */
  /* For All Elements */
  --var-1-animation: 300ms cubic-bezier(0.4, 0, 0.2, 1);
  /* For Nav Elements */
  --var-2-animation: transition 25s linear;
  --font-family: "DM Sans", sans-serif;
}

.footer-section {
  padding: 80px 0;
  max-width: 1280px;
  background-color: #ffffff;
  color: var(--color-bg-red);
  margin: auto;
}

a {
  text-decoration: none;
}

@media screen and (min-width: 375px) {
  .footer-container {
    display: flex;
    text-decoration: none;
    justify-content: space-around;
    flex-wrap: wrap;
  }
}
@media only screen and (min-width: 1280px) {
  .footer-right-side {
    display: flex;
  }
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.footer-img {
  margin-bottom: 5px;
}

.logo-link {
  fill: #ae1332;
  margin-right: 8px;
  transition: fill var(--var-2-animation);
}

.footer-logo-logo {
  display: flex;
  justify-content: center;
}

.footer-logo-link {
  padding: 0;
  transform: translate(0);
}

.footer-logo-text {
  color: #ae1332;
  transition: color var(--var-2-animation), border var(--var-2-animation);
}

.footer-logo-text:hover,
.footer-logo-text:focus {
  color: #a4cb66;
}

.logo-link {
  padding: 16px 0;
  color: #ae1332;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.3;
  text-transform: uppercase;
  display: flex;
}

.logo-link:hover,
.logo-link:focus {
  color: #a4cb66;
}

.logo-link:hover svg,
.logo-link:focus svg {
  fill: #a4cb66;
}

.footer-logo-img {
  margin-right: 8px;
  fill: #ae1332;
  transition: fill var(--var-1-animation);
}

.footer-nav {
  display: flex;
  justify-content: center;
  gap: 5px;
  margin-top: 24px;
  margin-bottom: 44px;
  transition: text-shadow var(--var-1-animation);
}

@media screen and (min-width: 375px) {
  .footer-nav {
    margin-top: 66px;
    margin-bottom: 0px;
    margin-bottom: 40px;
  }
}
@media only screen and (min-width: 1280px) {
  .footer-nav {
    margin-top: 80px;
    margin-bottom: auto;
    margin-left: 166px;
  }
}
.footer-nav-link {
  position: relative;
  transition: color var(--var-2-animation), text-shadow var(--var-2-animation);
}

.footer-nav-link {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: -0.04em;
  text-align: center;
  color: #ae1332;
  padding: 7px 8px;
}

.footer-nav-link:after {
  content: "";
  width: 0;
  height: 2px;
  position: absolute;
  right: 0;
  bottom: -4px;
  background: #a4cb66;
  transition: width 0.5s, left 0.5s;
}

.footer-nav-link:hover:after {
  width: 100%;
  left: 0;
}

.footer-nav a:hover,
.footer-nav a:focus {
  text-shadow: 1px 2px 3px #a4cb66;
  color: #a4cb66;
}

.footer-logo-text {
  color: #ae1332;
}

.footer-socials {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 44px;
}

@media screen and (min-width: 375px) {
  .footer-socials {
    justify-content: flex-start;
  }
}
@media only screen and (min-width: 1280px) {
  .footer-socials {
    justify-content: flex-start;
    margin-top: 0px;
    margin-bottom: 40px;
  }
}
.footer-socials-link {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #ae1332;
  border-radius: 10px;
  transition: background-color var(--var-1-animation);
}

.footer-socials-link {
