}

.footer-contacts {
  max-width: 143px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 14px;
  font-size: 14px;
  line-height: 1.29;
  text-align: center;
  font-style: normal;
  letter-spacing: -0.02em;
}

@media screen and (min-width: 375px) {
  .footer-contacts {
    max-width: 180px;
    gap: 20px;
    font-size: 18px;
    line-height: 1.33;
    text-align: start;
  }
  .logo-link {
    margin: 0 auto;
  }
}
.footer-address {
  display: block;
  width: 162px;
  color: #ae1332;
}

.footer-contacts-link {
  color: #ae1332;
  transition: color var(--var-1-animation), border var(--var-1-animation);
}

.footer-contacts-link:hover,
.footer-contacts-link:focus {
  color: #a4cb66;
}

@media only screen and (max-width: 375px) {
  .footer-img {
    max-width: 335px;
    max-height: 172px;
    margin: 0;
    width: 100%;
    height: 100%;
  }
  .footer-socials-link {
    width: 40px;
    height: 40px;
  }
  .footer-img-thumb {
    text-align: center;
    margin: 0 auto;
  }
  .footer-logo-logo {
    justify-content: center;
    display: flex;
    margin: 0;
  }
  .logo-link {
    margin: 0;
  }
  .footer-left-side {
    margin: 0;
  }
  .footer-address {
    width: auto;
  }
}
