  padding: 0;
  border: none;
  background-color: white;
  transform: scale(1);
  transition: scale 250ms 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-order-button:hover,
.modal-order-button:hover {
  transform: scale(1.2);
}

